import { Component, OnInit } from '@angular/core';
import python_docs from "../../assets/documentation.json";

enum eSelectionType {
    none = <any>'none',
    info = <any>'info',
    property = <any>'property',
    method = <any>'method',
}

@Component({
    selector: 'reference-component',
    templateUrl: './reference.component.html',
    styleUrls: ['./reference.component.scss']
})

export class ReferenceComponent implements OnInit {

    private _module: any;
    private _docs: any;
    private _hierarchy: any = [];
	private _selectionType: eSelectionType = eSelectionType.none;
	private _selectedProperty: any;
	private _selectedMethod: any;

    constructor() { }

    ngOnInit() {
        this._module = 'ixnetwork_restpy.testplatform.testplatform.TestPlatform';
        this._docs = python_docs[this._module];
		this._hierarchy.push({ module: this._module, class: this._docs['Class'] });
		this._selectionType = eSelectionType.info;
    }

    public get selectionType(): eSelectionType {
        return this._selectionType;
    }
    public get selectedProperty(): any {
        return this._selectedProperty;
    }
    public get selectedMethod(): any {
        return this._selectedMethod;
    }
    public get hierarchy(): any {
        return this._hierarchy;
    }
    public get module(): any {
        return this._module;
    }
    public get class(): any {
        return this._docs['Class']
    }
    public get summary(): any {
        return this._docs['Summary']
    }
    public get methods(): any {
        const methods = [];
        for (const method of this._docs['Methods']) {
            let returnsValue = method.Returns;
            let returnsLink = null;
            if (returnsValue !== undefined) {
                const start = returnsValue.indexOf('ixnetwork_restpy.');
                if (start !== -1) {
                    const end = method.Returns.indexOf(')', start);
                    returnsLink = method.Returns.substring(start, end);
                    returnsValue = returnsLink.split('.').pop();
                }
            }
            methods.push({
                name: method.Signature,
                shortName: this.getMethodShortName(method.Signature),
                description: method.Summary,
                returnsValue: returnsValue,
                returnsLink: returnsLink,
                args: method['Args'],
                raises: method['Raises']
            });
        }
        return methods;
    }
    getMethodShortName(method: string): string {
        const split = method.split('(');
        return split[0] + ' ()';
    }

    public get properties(): any {
        const results = [];
        for (const method of this._docs['Properties']) {
            let returnsValue = method.Returns;
            let returnsLink = null;
            if (returnsValue !== undefined) {
				const start = returnsValue.indexOf('ixnetwork_restpy.');
				const end = returnsValue.indexOf('.multivalue.Multivalue')
                if (start !== -1 && end === -1) {
                    continue;
				} 
                if (start !== -1 && end !== -1) {
                    const end = method.Returns.indexOf(')', start);
                    returnsLink = method.Returns.substring(start, end);
                    returnsValue = returnsLink.split('.').pop();
				}
            }
            results.push({
                name: method.Signature,
                description: method.Summary,
                readWrite: method.ReadWrite,
                returnsValue: returnsValue,
                returnsLink: returnsLink,
                raises: method['Raises']
            });
        }
        return results;
    }

    public get classproperties(): any {
        const results = [];
        for (const method of this._docs['Properties']) {
            let returnsValue = method.Returns;
            let returnsLink = null;
            if (returnsValue !== undefined) {
				const start = returnsValue.indexOf('ixnetwork_restpy.');
				const end = returnsValue.indexOf('.multivalue.Multivalue')
                if (start !== -1 && end === -1) {
                    const end = method.Returns.indexOf(')', start);
                    returnsLink = method.Returns.substring(start, end);
                    returnsValue = returnsLink.split('.').pop();

                    results.push({
                        name: method.Signature,
                        description: method.Summary,
                        readWrite: method.ReadWrite,
                        returnsValue: returnsValue,
                        returnsLink: returnsLink,
                        raises: method['Raises']
                    });
                }
            }
        }
        return results;
    }

    public async onClick(e: MouseEvent, item: any) {
        e.stopPropagation();
        this._selectionType = eSelectionType.none;
        this._module = item;
        this._docs = python_docs[item];
        for (const index in this._hierarchy) {
            if (this._hierarchy[index].module == item) {
                this._hierarchy.length = index;
                break;
            }
        }
        this._hierarchy.push({ module: this._module, class: this._docs['Class'] });
        this.showResults = false;
        this._selectionType = eSelectionType.info;
        document.getElementById('content').scrollTo(0, 0);
    }

    public searchValue: string = '';
    public results: any[] = [];
    public async onSearch(e: MouseEvent) {
        e.stopPropagation();
        this.results.length = 0;
        for (let key of Object.keys(python_docs)) {
            let id = 0;
            if (key.endsWith(this.searchValue)) {
                id = this.results.push(python_docs[key]);
            }
            else if (python_docs[key]['Summary'] != null && python_docs[key]['Summary'].indexOf(this.searchValue) != -1) {
                id = this.results.push(python_docs[key]);
            }
            if (id != 0) {
                this.results[id - 1]['Key'] = key;
            }
        }
        this.showResults = true;
    }

    public showResults: boolean = false;
    public async onCloseSearchResults(e: MouseEvent) {
        e.stopPropagation();
        this.showResults = false;
        this.results.length = 0;
    }

    private _selected: string;
    public get selected(): string {
        return this._selected;

    }
    onPropertySelected(e: MouseEvent, item: any) {
        e.stopPropagation();

        this._selectedProperty = item;
        this._selectionType = eSelectionType.property;
    }
    onPropertyClassSelected(e: MouseEvent, item: any) {
        this.onClick(e, item.returnsLink);
    }
    onMethodSelected(e: MouseEvent, item: any) {
        e.stopPropagation();

        this._selectedMethod = item;
        this._selectionType = eSelectionType.method;
    }

    onInitSelected(e: MouseEvent) {
        e.stopPropagation();

        this._selectionType = eSelectionType.info;
    }
}
